import { Icon, IconProps } from '@chakra-ui/icon';
import { forwardRef } from '@chakra-ui/system';

const SIZE_MAP = {
  sm: '1rem',
  md: '1.25rem',
  rg: '1.5rem',
  plus: '2rem',
  xp: '2.5rem',
  lg: '5rem',
};

interface Props extends IconProps {
  size?: keyof typeof SIZE_MAP;
}

const Component = forwardRef<Props, 'svg'>((props, ref) => {
  const { size } = props;
  const s = size ? SIZE_MAP[size] : '1em';

  return <Icon ref={ref} boxSize={s} {...props} />;
});

export { Component as Icon, type Props as IconProps };
